.customization-modal {
    position: fixed;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    background: white;
    border: 1px solid #ddd;
    padding: 20px;
    z-index: 1000;
    box-shadow: 0 0 15px rgba(0,0,0,0.2);
    width: 300px;
  }
  
  .customization-modal label {
    display: block;
    margin-top: 10px;
  }
  
  .customization-modal input,
  .customization-modal select {
    width: 100%;
    padding: 5px;
    margin-top: 5px;
  }
  
  .customization-modal button {
    margin-top: 15px;
    padding: 8px 12px;
  }